<template>
    <li>
        <span v-bind:class="{done: todo.completed}">
            <input type="checkbox" @change="todoDone" @keydown.enter="todoDone">
            <strong>{{todo.id}}</strong>
            {{todo.title}}
        </span>
        <button class="rm" v-on:click="$emit('remove-todo', todo.id)">&times;</button>
    </li>
</template>

<script>
import store from '@/store';
export default {
    props: {
        todo: {
            // type: Object,
            // required: true
        }
    },
    methods: {
        todoDone() {
            store.commit('todoDone', this.todo.id)
        }
    }
}
</script>

<style scoped>
li {
    border: 1px solid #ccc;
    display: flex;
    justify-content: space-between;
    padding: .5rem 2rem;
    margin-bottom: 1rem;
}
.rm {
    border-radius: 50%;
    background-color: red;
    color: white;
    border: none;
    font-weight: bold;
}
input {
    margin-right: 1rem;
}
.done {
    text-decoration: line-through;
}
</style>