<template>
    <div>
        <ul>
            <ToDoItem
            v-for="todo of todos"
            v-bind:todo="todo"
            v-on:remove-todo="removeTodo"
            />
        </ul>
    </div>
</template>

<script>
import ToDoItem from '@/components/ToDoItem.vue';
import store from '@/store';
export default {
    components: {
        ToDoItem
    },
    computed: {
      todos() {
          return store.getters.todos
      }
  },
    methods: {
        removeTodo(id) {
            this.$emit('remove-todo', id);
        }
    }
}
</script>

<style scoped>
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
</style>