<template>
    <form
    v-on:submit.prevent="onSubmit"
    >
        <input type="text" v-model="title">
        <button type="submit">Create</button>
    </form>
</template>

<script>
import store from '@/store'
export default {
    data() {
        return {
            title: ''
        }
    },
    computed: {
        todos() {
            return this.$store.getters.todos
        }
    },
    methods: {
        onSubmit() {
            const newTodo = {
                id: Math.random(100),
                title: this.title,
                completed: false,
            }
            this.title = '';
            // console.log(store)
            // console.log(newTodo.title)
            store.commit('addTodo', newTodo);
        }
    }
}
</script>